import React, {useEffect, useState} from 'react';
import './App.css';
import Recipe from './Recipe'


const App = () => {

  const APP_ID = "8c2b11d2";
  const APP_KEY = "c88bfac347009b64cbde5ada4bdaa817";

  const [recipes, setRecipes] = useState([]);
  const [search, setSearch] = useState("");
  const [query, setQuery] = useState('chicken');

  useEffect(() => {
    getRecipes();
  }, [query]);

  const getRecipes = async () => {
    const response = await fetch(`https://api.edamam.com/search?q=${query}&app_id=${APP_ID}&app_key=${APP_KEY}`)
    const data = await response.json(); //you can use await or .then for promises
    setRecipes(data.hits);


    //so it could've been written as:
    // fetch('URL')
    //.then(response => {.then response.json}...)
    //
  };

  const updateSearch = e => {
    setSearch(e.target.value);
  }

  const getSearch = e => {
    e.preventDefault();
    setQuery(search);
    setSearch('');
  }

  return(
    <div className="App">
      <div className="welcome">
        <h1>Recipe Finder</h1>
        <p>Search for new meals to spice up your life! Begin your search below.</p>
      </div>
      <form onSubmit={getSearch} className="search-form"> 
        <span role="img" aria-label="Magnifier">üîç</span>
        <input 
          className="search-bar" 
          type="text"
          placeholder="Find Your Next Meal"
          value={search}
          onChange = {updateSearch}
        />
        <button className="search-button" type="submit">
          Search for Meals
          </button>
      </form>

      <div className="suggestions">
        <h1>Some suggestions for you...</h1>
      </div>

      <div className="recipes">
        {recipes.map(recipe => (
          <Recipe 
            key={1234}
            title={recipe.recipe.label} 
            calories={recipe.recipe.calories} 
            image={recipe.recipe.image}
            ingredients={recipe.recipe.ingredients}
            recipeUrl={recipe.recipe.url}
          />
        ))}
      </div>
    </div>
  );
};

export default App;
